import { Component, ViewChild, Input, SimpleChanges, OnInit } from '@angular/core';
import { DataSource } from '@angular/cdk/table';
import { MdPaginator, MdSort } from '@angular/material';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import { Quote } from "../model/quote";
import { QuoteService } from "../service/quote.service";
import { Router } from '@angular/router';
@Component({
  selector: 'app-quote',
  templateUrl: './quote.component.html',
  styleUrls: ['./quote.component.css']
})

export class QuoteComponent implements OnInit {
  quotes = Array<Quote>();
  constructor(private quoteService: QuoteService, private router: Router) { }

  @Input() stsymbol: string;
  ngOnChanges(changes: SimpleChanges) {
    if (this.stsymbol)
      this.quoteService.getQuotes(this.stsymbol).subscribe((p) => {
        this.updateOrPush(p);

        console.log(p);
      });
  }
  ngOnInit() {
    let favStock = ['spy', 'vti', 'goog', 'amzn', 'aapl', 'stor', 'gld', 'msft', 'fb', 'dis', 'luv', 'wmt', 'bac', 'syf'];
    favStock.forEach(element => {
      this.quoteService.getQuotes(element).subscribe((p) => {
        this.quotes.push(p);
      });

    });
    this.router.navigate(['/main', favStock[0].toUpperCase()]);
    this.refreshval();
  }

  refreshval() {
    console.log("refresh called");
    setTimeout(() => this.refreshval(), 10000);
    this.quotes.forEach(element => {
      this.quoteService.getQuotes(element.symbol).subscribe((p) => {
        this.updateOrPush(p);
      });
    });
  }

  updateOrPush(p: Quote) {
    let found = false;
    for (let i = 0; i < this.quotes.length; i++) {
      if (this.quotes[i].symbol == p.symbol) {
        this.quotes[i].latestPrice = p.latestPrice;
        this.quotes[i].changePercent = p.changePercent;
        found = true;
      }
    }

    if (found == false)
      this.quotes.push(p);
  }
}



